import { UsuariosService } from './../usuarios.service';
import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/model/usuario';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscription, observable } from 'rxjs';

@Component({
  selector: 'app-usuarios-list',
  templateUrl: './usuarios-list.component.html',
  styleUrls: ['./usuarios-list.component.css']
})
export class UsuariosListComponent implements OnInit {

  constructor(private service: UsuariosService, private router: Router, private route: ActivatedRoute) { }

  usuarios: Usuario[];
  inscricao: Subscription;

  ngOnInit() {

    // Recebe uma lista de usuários
    // this.usuarios = this.service.list();
    this.service.list().subscribe(
      dados => this.usuarios = dados)
  }

  // Funcão redirecionará o usuário para a tela de edição de usuário
  onEdit(id: number) {
    if (id != null ) {
      this.router.navigate(['usuarios/' + id]);
    }
  }

  onDelete(id: number) {
    if (id != null) {
      this.service.deleteUsuario(id);
    }
  }

  onNew() {
    this.router.navigate(['usuarios/novo']);
  }
}
